export function initWebPageWithData() {
  fetch("./../static/data/banque_postale.json")
    .then((response) => response.json())
    .then((data) =>
      populateWebpageWithData(
        data.name,
        data.description,
        data.phoneNumber,
        data.mail
      )
    );
}

export function populateWebpageWithData(
  bankName,
  bankDescription,
  bankPhone,
  bankMail
) {
  document.getElementById('bankNameContainer').htmlContent = bankName;
  document.getElementById('bankDescriptionContainer').htmlContent = bankDescription;
  document.getElementById('phoneNumberContainer').htmlContent = bankPhone;
  document.getElementById('mailContainer').htmlContent = bankMail;
}
